const Document = () => {
  const { control } = useFormContext();
    const { register, setValue } = useFormContext();
    const [imagePreview, setImagePreview] = useState(null);

    const handleFileChange = (event) => {
      const file = event.target.files[0];

      if (file) {
        // Create a new FileReader object
        const reader = new FileReader();

        // Set the callback function for when the file is loaded
        reader.onload = (event) => {
          // Set the image preview to the loaded image data URL
          setImagePreview(event.target.result);
          // Set the form value to the uploaded file
          setValue("photo", file);
        };

        // Read the file as a data URL
        reader.readAsDataURL(file);
      }
    };
  return (
    <>
      <Box>
        <Card>
          {imagePreview ? (
            <CardMedia
              component="img"
              src={ imagePreview}
              height="200"
              alt="Selected Image Preview"
            />
          ) : (
            <CardContent>
              <Typography variant="body1">
                Select an Image to Preview
              </Typography>
            </CardContent>
          )}
          <CardContent>
            <Button variant="contained" component="label">
              Upload File
              <input
                type="file"
                hidden
                {...register("photo")}
                onChange={handleFileChange}
              />
            </Button>
          </CardContent>
        </Card>
      </Box>
    </>
  );
};
function getStepContent(step, sectionobj, classObj) {
  switch (step) {
    case 0:
      return <BasicForm sectionobj={sectionobj} classObj={classObj} />;

    case 1:
      return <ContactForm />;
    case 2:
      return <PersonalForm />;
    case 3:
      return <PaymentForm />;
    case 4:
      return <Document />;
    default:
      return "unknown step";
  }
}

export default StudentAdmissionForm;
